import React, { useState } from "react";
import Card from "./components/Card";
import Modal from "./components/Modal";
import {
  calcularNumeroDeVida,
  calcularNumeroDaPersonalidade,
  calcularNumeroDaAlma,
  calcularAnoPessoal,
  calcularLicoesCarmicas,
  calcularNumerosKarmicos,
  gerarNumeroDoDia,dicasNumerologia
} from "./utils/numerologyUtils";
import { getAllDetailedExplanations } from "./utils/explanations";
import './App.css';
import emailjs from 'emailjs-com';

export default function App() {
  const [dataNascimento, setDataNascimento] = useState("");
  const [nomeCompleto, setNomeCompleto] = useState("");
  const [modalInfo, setModalInfo] = useState({ open: false, title: "", content: "" });
  const [email, setEmail] = useState("");
  const [modoDetalhado, setModoDetalhado] = useState(false);
  
  const alternarModoExplicacao = () => {
    const novoModo = !modoDetalhado;
    setModoDetalhado(novoModo);

    // Atualiza o conte√∫do do modal com o novo modo
    setModalInfo((prev) => ({
      ...prev,
      content: novoModo ? prev.deep : prev.simple,
    }));
  };

  const mostrarExplicacao = (titulo, valor) => {
    if (!dataNascimento && !nomeCompleto) return;

    const explicacoes = getAllDetailedExplanations({
      name: nomeCompleto,
      birthDate: dataNascimento,
    });

    const selecionado = explicacoes.find((e) => e.title === titulo);
    if (selecionado) {
      setModalInfo({
        open: true,
        title: `${selecionado.title} - ${valor}`,
        content: modoDetalhado ? selecionado.deep : selecionado.simple,
        simple: selecionado.simple,
        deep: selecionado.deep
      });
    }
  };

  const renderValor = (valor) => (
    <div style={{
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      gap: 8,
      fontWeight: "bold",
      fontSize: 20,
      textAlign: "center",
      cursor: "default",
      padding: 8,
      borderRadius: 6,
      background: "#ffffff"
    }}>
      <span>{valor.value}</span>
      <span
        onClick={() => mostrarExplicacao(valor.title, valor.value)}
        style={{
          cursor: "pointer",
          fontSize: 18,
          color: "#6c63ff", // tom de roxo
          border: "1px solid #6c63ff",
          borderRadius: "50%",
          width: 22,
          height: 22,
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          fontWeight: "bold",
          userSelect: "none"
        }}
        title="Ver explica√ß√£o"
      >
        i
      </span>
    </div>
  );

  const enviarEmail = async () => {
    if (!email) {
      alert("Por favor, insira seu email.");
      return;
    }
    if (!nomeCompleto || !dataNascimento) {
      alert("Por favor, preencha seu nome completo e data de nascimento.");
      return;
    }

    // Pegando todas as explica√ß√µes detalhadas
    const explicacoes = getAllDetailedExplanations({
      name: nomeCompleto,
      birthDate: dataNascimento,
    });

    // Construindo a mensagem concatenando todos os conte√∫dos detalhados
    const textoExplicacoes = explicacoes
      .map((exp) => `T√≠tulo: ${exp.title}\nExplica√ß√£o: ${exp.deep}\n`)
      .join("\n--------------------\n");

    const emailData = {
      to: email,
      subject: "Numerologia",
      message: `Nome Completo: ${nomeCompleto}\nData de Nascimento: ${dataNascimento}\n\nExplica√ß√µes:\n${textoExplicacoes}`,
    };

    try {
      const response = await fetch("https://seu-backend.com/send-email", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(emailData),
      });

      if (response.ok) {
        alert("Email enviado com sucesso!");
        setEmail("");
      } else {
        const error = await response.json();
        alert("Erro ao enviar email: " + (error.message || "Erro desconhecido"));
      }
    } catch (error) {
      alert("Erro na conex√£o com o servidor.");
    }
  };

  return (
    <div className="background">
      <header className="header">
        <div>
          <h1 className="headerText">üî¢Numerologia</h1>
          <h2 className="headerText">üîÆ Descubra os mist√©rios ocultos nos n√∫meros do seu nome e nascimento üîÆ</h2>
        </div>
      </header>
        <div
          style={{
            maxWidth: "100%",
            margin: "2rem auto",
            padding: "0 1rem",
            display: "grid",
            gap: "1.5rem",
            gridTemplateColumns: "repeat(auto-fit, minmax(280px, 1fr))",
            boxSizing: "border-box"
          }}
        >
          <Card title="Informa√ß√µes Pessoais" description="Digite seus dados para calcular os n√∫meros">
            <label htmlFor="dataNascimento" style={{ display: "block", marginBottom: 8 }}>
              Data de Nascimento:
            </label>
            <input
              type="date"
              id="dataNascimento"
              value={dataNascimento}
              onChange={(e) => setDataNascimento(e.target.value)}
              style={{ width: "100%", padding: 8, borderRadius: 6, border: "1px solid #ccc" }}
            />

            <label htmlFor="nomeCompleto" style={{ display: "block", marginTop: 12, marginBottom: 8 }}>
              Nome Completo:
            </label>
            <input
              type="text"
              id="nomeCompleto"
              value={nomeCompleto}
              onChange={(e) => setNomeCompleto(e.target.value)}
              placeholder="Seu nome completo"
              style={{ width: "100%", padding: 8, borderRadius: 6, border: "1px solid #ccc" }}
            />
          </Card>

          {/* Cada Card passa valor.title para o click */}
          <Card title="N√∫mero de Vida" description="Baseado na sua data de nascimento">
            {dataNascimento
              ? renderValor({ title: "N√∫mero de Vida", value: calcularNumeroDeVida(dataNascimento) })
              : <p style={{ opacity: 0.7, textAlign: "center" }}>Preencha a data de nascimento</p>}
          </Card>

          <Card title="N√∫mero da Personalidade" description="Caracter√≠sticas externas (consoantes)">
            {nomeCompleto
              ? renderValor({ title: "N√∫mero da Personalidade", value: calcularNumeroDaPersonalidade(nomeCompleto) })
              : <p style={{ opacity: 0.7, textAlign: "center" }}>Preencha o nome completo</p>}
          </Card>

          <Card title="N√∫mero da Alma" description="Motiva√ß√µes internas (vogais)">
            {nomeCompleto
              ? renderValor({ title: "N√∫mero da Alma", value: calcularNumeroDaAlma(nomeCompleto) })
              : <p style={{ opacity: 0.7, textAlign: "center" }}>Preencha o nome completo</p>}
          </Card>

          <Card title="Ano Pessoal" description="Ciclo pessoal atual">
            {dataNascimento
              ? renderValor({ title: "Ano Pessoal", value: calcularAnoPessoal(dataNascimento) })
              : <p style={{ opacity: 0.7, textAlign: "center" }}>Preencha a data de nascimento</p>}
          </Card>

          <Card title="Li√ß√µes C√°rmicas" description="Li√ß√µes a aprender nesta vida">
            {nomeCompleto
              ? renderValor({
                  title: "Li√ß√µes C√°rmicas",
                  value: calcularLicoesCarmicas(nomeCompleto).join(", ") || "Nenhuma",
                })
              : <p style={{ opacity: 0.7, textAlign: "center" }}>Preencha o nome completo</p>}
          </Card>

          <Card title="N√∫mero K√°rmico" description="Desafios espirituais (caso existam)">
            {nomeCompleto
              ? (() => {
                  const { karmico } = calcularNumerosKarmicos(nomeCompleto);
                  return renderValor({
                    title: "N√∫mero K√°rmico",
                    value: karmico || "Nenhum",
                  });
                })()
              : <p style={{ opacity: 1, textAlign: "center" }}>Preencha o nome completo</p>}
          </Card>

            {nomeCompleto && dataNascimento && (
            <Card title="üîÆ Dica do Dia" description="Orienta√ß√£o numerol√≥gica para hoje">
              {(() => {
                const numeroVida = calcularNumeroDeVida(dataNascimento);
                const numeroDia = gerarNumeroDoDia(numeroVida);
                const dica = dicasNumerologia[numeroDia];

                return (
                  <div style={{ textAlign: "center", fontStyle: "italic", padding: "1rem" }}>
                    <p style={{ fontSize: 20,color:"white" }}>N√∫mero do Dia: <strong>{numeroDia}</strong></p>
                    <p style={{ marginTop: 8,color:"white" }}>{dica}</p>
                  </div>
                );
              })()}
            </Card>
          )}

          {/** 
          <Card title="Email" description="Coloque o seu email para receber o relat√≥rio completo.">
            <label htmlFor="email" style={{ display: "block", marginTop: 12, marginBottom: 8 }}>
              Email:
            </label>
            <input
              type="text"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Coloque aqui o seu Email:"
              style={{ width: "100%", padding: 8, borderRadius: 6, border: "1px solid #ccc" }}
              />
            <button type="submit" onClick={enviarEmail}>Enviar Informa√ß√µes</button>
          </Card>
          */}

          <Modal
            open={modalInfo.open}
            title={modalInfo.title}
            content={modalInfo.content}
            onClose={() => setModalInfo({ open: false, title: "", content: "" })}
            extraButton={{
              label: modoDetalhado ? "Ver Explica√ß√£o Simples" : "Ver Explica√ß√£o Detalhada",
              onClick: alternarModoExplicacao
            }}
          />

        </div>
        <footer className="footer">
          <p className="footerText">¬© 2025 Numerologia. Todos os direitos reservados. Este site √© apenas para fins informativos e de entretenimento.</p>
        </footer>
    </div>
  );
}
